@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lalezar:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap");

*{  /*  */
  margin:0;
  padding:0;
}
html,body{
  font-size: 10px;
  width:100%;
  height:100%;
}
html{
  /* ************************************************************
      :overflow(,)/overflow-x()/overflow-y()
      :auto/scroll/hidden
      ************************************************************ */
  overflow-y:scroll; /*      */
  overflow-x:hidden; /*      */
}

article,aside,details,figcaption,figure,
footer,header,hgroup,menu,nav,section{
  display:block;
  /*   
  ->        */
}

ul{margin:0;padding:0}
li{list-style:none;}   /*   */

body,input,textarea{
  font-size:12px;
  font-weight:100; /* IE8   */
  font-family:'Noto Sans KR', sans-serif;
}
img{
  /* vertical-align:top; */
  font-size:0;
  border:0; /*       */
}
table{border-collapse:collapse;} /*          */
input,select{vertical-align:middle;} /*  */

.clear:after{    /*   float       */
  content:"";
  display:block;
  clear:both;
}
.indent{     /*    */
  display:block;
  text-indent:-9999px;
}
.hidden{display:none;}

a{
  color: inherit;
  text-decoration:none;
}

a:visited {
  color: inherit;
  text-decoration: none;
}

a:hover {
  color: inherit;
}

input {
  padding-left: 1rem;
}

input:focus {
  outline:none;
  border: 2px solid #721ea6;
  transition: .1s linear;
}